    background-color: rgb(238, 244, 249);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    --lightest: rgb(238, 244, 249);
    --light-blue: rgb(142, 180, 209);
    --mid-blue: #005fb8;
    --dark: #002a52;

    /* padding: 5% 10%; */

    display: flex;
    justify-content: center;
}

body {
    display: flex;
    flex-direction: column;
    justify-content: center;

}

header {
    display: flex;
    color: var(--mid-blue);
    font-size: larger;
    align-items: center;
    flex-direction: column;
}

#city-search {
    font-size: 1.2em;
    padding-left: 6px;
    box-sizing: border-box;
    border-top: none;
    border-right: none;
    border-left: none;
    border-bottom-color: var(--dark);

    background-color: rgba(0, 0, 0, 0);
}

#search-container {
    display: flex;
}

#submit-search {
    cursor: pointer;
    content: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    border: none;
    background: none;

}

#city-search:focus {
    border-bottom-width: 3px;
    outline: none;
    background-color: rgba(0, 0, 0, 10%);
}

#main-display-container {
    display: none;
}

#temp {
    margin: 7px 0;
}

[data-unit='f']:after {
    content: ' F'
}

[data-unit='c']:after {
    content: ' C'
}

button {
    cursor: pointer;
}

#current-conditions {
    box-sizing: border-box;
    font-size: 1.1em;
    background-color: var(--light-blue);
    margin-top: 10px;
    padding: 7px 10px 2px 10px;
    max-width: max-content;

    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 1fr;
    grid-auto-columns: auto;
    gap: 30px;

    & h2 {
        margin-top: 0;
    }

    & p {
        margin-bottom: 0;
    }
}

#current-conditions-main {
    display: grid;
    grid-template-rows: auto 60px auto 45px 25px auto;
    /* grid-template-rows: auto 40px; */

    grid-template-columns: auto 40px;

    & #city {
        grid-row: 1/2;
        margin-bottom: 5px
    }

    & #datetime {
        grid-row: 2/3;
        margin: 5px 0;
    }

    & #temp {
        grid-row: 3/4;
    }

    & #hi-low-temp {
        grid-row: 4/5;
    }

    & #unit-switch {
        grid-row: 5/6;
        justify-self: start;
    }

    & #conditions {
        grid-row: 6/7;
    }
}

#expand-details-btn {
    align-self: end;
    grid-row: 6/7;
    grid-column: 2/3;
    cursor: pointer;
    border: none;
    content: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

.details-shown {
    transform: rotate(180deg);

}

#current-conditions-details {
    justify-items: end;
    text-align: center;
    width: auto;
    font-size: .8em;
    /* background-color: hsl(206, 42%, 79%); */

    & span {
        font-weight: bolder;
        font-size: 1.4em;
    }

    & h3 {
        justify-self: center;
        margin: 2px 0 0 0;

    }

}

#unit-switch {
    /* flex: 0 0 auto; */

    box-sizing: border-box;
    background-color: var(--light-blue);
    border: none;
    border-radius: 8px;
    height: 26px;
    padding: 10px auto;

    & span {
        padding: 2px 6px;
    }

}

.button-unit {
    font-size: larger;
    color: var(--dark);
}

.button-unit.unit-selected {
    font-weight: bold;
    background-color: var(--dark);
    color: white;
    border-radius: 8px;
    padding: 12px;
}

#today-hourly-conditions {
    display: grid;
    overflow: auto;
    gap: 7px;
    grid-template-columns: repeat(24, 1fr);
    max-width: 50vw;
    padding: 2px 5px;
}

.hourly-card {
    box-sizing: border-box;
    width: 150px;
    padding: 8px;
    background-color: var(--light-blue);

    & h3 {
        margin-top: 0;
    }
}

#seven-day {
    display: grid;
    overflow: auto;

    gap: 7px;
    grid-template-columns: repeat(7, 1fr);
    /* border: 1px solid black; */
    max-width: 50vw;
    padding: 2px 5px;
}

/* width */
::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: var(--mid-blue);
    border-radius: 3px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: var(--dark);
}


.seven-day-card {
    box-sizing: border-box;
    /* border: solid black 1px; */
    width: 150px;
    padding: 8px;
    background-color: var(--light-blue);

    & h3 {
        margin-top: 0;
    }
}

[data-icon]:before {
    display: block;
    min-width: 50px;
    max-width: 80px;
    min-height: 50px;
    max-height: 80px;
}

[data-icon='clear-day']:before {
    content: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
    /* max-width: 50px;
    max-height: 50px; */
}

[data-icon='partly-cloudy-day']:before {
