[data-uv='1'],
[data-uv='2']:after {
    content: " (Low)";
    color: #029000;
}

[data-uv='3'],
[data-uv='4'],
[data-uv='5']:after {
    content: " (Mild)";
    color: #b8b800;
}

[data-uv='6'],
[data-uv='7']:after {
    content: " (High)";
